import Ghost from '../ghost';

//stupid ghost

class g1 extends Ghost {
  constructor(game) {
    super(game);
    this.color = '#66846c';
    this.pos = this.mazeNodes[12][14].spritePos();
  }

  move() {
    let potentialMoves = [];

    const offsetX = this.vel[0] * 1000/60;
    const offsetY = this.vel[1] * 1000/60;
    this.pos = [this.pos[0] + offsetX, this.pos[1]];
  }

  reset() {
    this.pos = this.mazeNodes[12][14].spritePos();
  }
}

export default g1;



